  /* display: flex;
  flex-direction: row; */
}
.container {
  margin: 0 auto;
  padding: 15px;
  max-width: 290px;
}
.add-new-item {
  position: fixed;
  top:0; right: 0; bottom: 0; left: 0;
  padding: 15px;
  background: #FFF;
}
.expanded {
  border: 1px solid #CCC;
}
.collapsed {
  display: none;
}
.form-toggle {
  float: right;
}

.button {
  border: none;
  outline: none;
  margin: 5px 0;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  transition: all 0.25s ease;
  overflow: hidden;  
  position: relative;
  border-radius: 50px;

  &:hover {
    filter: brightness(110%);
    //transform: translate(-3px, -3px) scale(1.02);
    box-shadow: 2px 3px 3px 0 rgba(100, 100, 100, 0.6);
  }
  &--blue {
    background: blue;
    color: white;
  }
  &--secondary {
    &--blue {
      background: white;
      border: 1px solid blue;
      color: blue;
    }
    /* &--red {
      background: white;
      border: 1px solid @color-red;
      color: @color-red;
    } */
  }
  &--text {
    font-size: 1em;
    padding: 0 20px;
  }
  &--add-item {
    position: absolute;
    bottom: 30px;
    right: 30px;
    width: 50px;
  }
  &--close {
    transform: rotate(45deg);
    float: right;
    color: #333;
    background: none;
    width: 50px;
  }
  i {
    font-size: 2.5em;
  }
}

nav {
  margin: 30px auto;
  a {
    display: inline-block;
    padding: 15px 20px;
    text-decoration: none;

    &:hover, &:active {
      color: blue;
    }
    &.active {
      border-bottom: 2px solid blue;
    }
  }
}
